import React from 'react';
 import { Container,Row,Col } from 'react-bootstrap';
 import 'bootstrap/dist/css/bootstrap.css';
 import 'bootstrap/dist/css/bootstrap-grid.css';
 


function InitWelcome() {
 
   return(
           <Container fluid className=" nuevocontainer "  >
           <Row >
             <Col className=" p-5"  >
             <h2 style={{ background:' rgba(107, 5, 110, 0.5)' }} className=" text-white p-5 font-weight-bold rounded text-center justify-content-center">BIENVENIDO A MI TIENDA ONLINE</h2>
             </Col>
           </Row>
           </Container>
   );
}

export default InitWelcome;
