import React from 'react';
import {Navbar,Nav,Form,FormControl,Button,DropdownButton,Dropdown,ButtonGroup } from 'react-bootstrap';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';


function SearchBar(){
return(
<div>
<Navbar className="container align-content-center align-items-center" bg="light" variant="light">
      <Nav className="col-sm-4 col-md-6 col-lg-10 col-xl-12 bg-white"  > 
      <img src="https://imgreposwx.files.wordpress.com/2020/09/tienda-facil-logo.png"  width="200px" heigth="200px" alt="logo"/>
         <Form inline className="justify-content-center bg-white  form-group col-sm-4 col-md-6 col-lg-10 " >
         <Button  variant="outline-danger" className=" btn btn-sm mr-2">Search</Button>
         <FormControl  type="text" placeholder="Search" className="mr-2 form-control form-control-sm col-sm-4 col-md-6 col-lg-8 form-inline" />
        </Form>
     </Nav>
</Navbar>
<Navbar collapseOnSelect expand="lg" className="container justify-content-center" bg="light" variant="light">
<Navbar.Toggle aria-controls="responsive-navbar-nav" />
<Navbar.Collapse id="responsive-navbar-nav">
<Nav className="justify-content-center col-xl-12" justify variant="tabs" defaultActiveKey="/home">
    <ButtonGroup >
      <DropdownButton as={ButtonGroup} title="Categoria" id="bg-nested-dropdown">
         <Dropdown.Item eventKey="1">Accesorios</Dropdown.Item>
         <Dropdown.Item eventKey="2">Ropa</Dropdown.Item>
         <Dropdown.Item eventKey="3">Muebles</Dropdown.Item>
      </DropdownButton>
    </ButtonGroup>
  <Nav.Item>
  
       <Link className="nav-link" to="/forstores/home">Inicio</Link> 
     
  </Nav.Item>
  <Nav.Item >
 
      <Link className="nav-link" to="/forstores/weus">Nosotros</Link> 
    
  </Nav.Item>
  <Nav.Item  > 
  
      <Link className="nav-link" to="/forstores/contact">Contacto</Link>  
     
  </Nav.Item>
    <Nav.Item  > 

      <Link className="nav-link" to="/forstores/location">ubicacion</Link>  
     
  </Nav.Item>
</Nav>
</Navbar.Collapse>
</Navbar>
</div>
)
}
export default SearchBar;
