import React from 'react'
import {Form,Button,Container,Row,Col} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';

 function Contact(){

 
return(
<Container fluid className=" nuevocontainer">
<Row>
    <Col className="pendinte "  >
    <h2 style={{ background:' rgba(107, 5, 110, 0.5)' }} className=" mt mt-xl-5 mt-lg-4 mt-md-4 mt-sm-2 text-white p-2 font-weight-bold rounded text-center justify-content-center text-uppercase">Contactanos </h2>
       
      </Col>
  </Row>
  <Row>
    <Col className="pendinte ">
    <img
      className="img-responsive"
      src="https://imgreposwx.files.wordpress.com/2020/09/bannerstores3.jpg"
      alt="First slide"
    />
    </Col>
    <Col  >
    <button type="button" className="btn btn-primary" data-toggle="collapse" data-target="#demo">ingresa tus datos aqui</button>
  <div id="demo" className="collapse">
  <Form id="contact-form" onSubmit='#' method="POST">
    <div className="form-group">
        <label htmlFor="name">Name</label>
        <input type="text" className="form-control" />
    </div>
    <div className="form-group">
        <label htmlFor="exampleInputEmail1">Email address</label>
        <input type="email" className="form-control" aria-describedby="emailHelp" />
    </div>
    <div className="form-group">
        <label htmlFor="message">Message</label>
        <textarea className="form-control" rows="5"></textarea>
    </div>
    <Button type="submit" className="btn btn-primary">Submit</Button>
</Form>
    
  </div>
</Col>
  </Row>
  </Container>

)
}
export default Contact;